import React from 'react';

const Characteritem = ({ item }) => {
	console.log(item);
	return (
		<div className="card">
			<div className="card-inner">
				<div className="card-front">
					{/* {item.web_name} */}
					<img src={item.photo} alt={item.web_name} />
				</div>
				<div className="card-back">
					<h1>FPL Player Card 2021/2022</h1>
					<ul>
						<li>
							<strong>First Name:</strong>{' '}
							{item.first_name}
						</li>
						<li>
							<strong>Second Name:</strong>{' '}
							{item.second_name}
						</li>
						<li>
							<strong>Assists this season:</strong>{' '}
							{item.assists}
						</li>

						<li>
							<strong>Goals this season:</strong>{' '}
							{item.goals_scored}
						</li>
						<li>
							<strong>latest news:</strong> {item.news}
						</li>
					</ul>
				</div>
			</div>
		</div>
	);
};

export default Characteritem;
